<template>
  <div class="">
    <mt-button type="default" @click.native="handleClick">赵岩欢迎你</mt-button>
    <!--<mt-button type="primary" @click.native.self="shareApp()">浏览器自带</mt-button>-->
  </div>
</template>

<script>
  export default {
    name: '',
    data () {
      return {}
    },
    mounted(){

    },
    methods: {
      shareApp(){
        var nativeShare = new NativeShare()
        var shareData = {
          title: 'Shadow',
          desc: 'Shadow分享',
          // 如果是微信该link的域名必须要在微信后台配置的安全域名之内的。
          link: 'https://shadownc.github.io/shareNative/',
          icon: 'http://img3.imgtn.bdimg.com/it/u=3647609273,686575484&fm=27&gp=0.jpg',
          // 不要过于依赖以下两个回调，很多浏览器是不支持的
          success: function () {
            alert('success')
          },
          fail: function () {
            alert('fail')
          }
        }
        nativeShare.setShareData(shareData)
        try {
          nativeShare.call()
        } catch (err) {
          // 如果不支持，你可以在这里做降级处理
          alert(err.message)
        }
      },
      handleClick(){
        this.$messagebox.confirm('确定跳转吗').then(action => {
          if (action == 'confirm') {
            this.$router.push({name: 'ShareDetail'})
          } else {
            return false
          }
        })
      }
    }
  }
</script>


<style scoped>

</style>



